            name:"北京电影学院（云南）培训中心",
            address_1:"昆明市西山区",
            address_2:"滇池时代广场B座5楼"
        },
        {
            name:"七彩云南One Work",
            address_1:"昆明市呈贡区",
            address_2:"七彩云南第一城166天阶3A层one work 路演大厅"
        },{
            name:"西门潮街",
            address_1:"昆明市官渡区",
            address_2:"珥季路大都摩天购物中心负二楼西门潮街书吧"
        },
        {
            name:"言几又 1903店",
            address_1:"昆明市西山区",
            address_2:"海宏路公园1903云湖左岸9号2栋"
        },
      ]
    };
  },
  computed:{
    show_sites_rows(){
      const cols_num = 4;
      var _show_sites_rows=[];
      var cur = [];
      this.show_sites.forEach(site => {
        cur.push(site);
        if(cur.length==cols_num){
          _show_sites_rows.push(cur);
          cur=[];
        }
      });
      if(cur.length!=0){
        _show_sites_rows.push(cur);
      }
      console.log(_show_sites_rows)
      return _show_sites_rows;
    }
  },
  methods: {
    onSlideStart() {
      this.sliding = true;
    },
    onSlideEnd() {
      this.sliding = false;
    },
  },
};
</script>

